import React from 'react'

const Contact = () => {
  return (
    <>
    <div className="wrapper">
      <section className="youcan">
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <div className="mainHeading">
                <h1>Let's Talk For The Next Project</h1>
              </div>
              <div className="contactcard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/icons/support.svg"
                    alt="support"
                  />
                </figure>
                <a href="mailto:info@netspacesoftware.in"
                  >info@netspacesoftware.in</a
                >
              </div>
              <div className="contactcard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/icons/call2.svg"
                    alt="call"
                  />
                </figure>
                <p>+91734-4060860, +91 6262558866</p>
              </div>
              <div className="contactcard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/icons/address.svg"
                    alt="address"
                  />
                </figure>
                <p>
                  38 Ghat Kar Par Marg, Near BJP Office Freeganj, Ujjain, Madhya
                  Pradesh, 456010, India
                </p>
              </div>
            </div>
            <div className="col-md-6">
              <div className="contactInfo">
                <h5>GET IN TOUCH</h5>
                <form
                  className="contactFeild"
                  method="post"
                  action="https://www.netspacesoftware.in/netspace-software-contact-us"
                  enctype="multipart/form-data"
                >
                  <div className="row">
                    <div className="col-md-12">
                      <div className="contCard">
                        <div className="form-group">
                          <label for="">Name</label
                          ><span style={{color: 'red'}} className="required">*</span>
                          <input
                            placeholder="Full Name"
                            className="form-control"
                            type="text"
                            name="name"
                            required
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="contCard">
                        <div className="form-group">
                          <label for="">Email Address</label>
                          <input
                            placeholder="Email"
                            className="form-control"
                            type="email"
                            name="email"
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="contCard">
                        <div className="form-group">
                          <label for="">Mobile No</label
                          ><span style={{color: 'red'}} className="required">*</span>
                          <input
                            placeholder="Enter Mobile Number"
                            className="form-control"
                            type="number"
                            name="mobile_no"
                            required
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="contCard">
                        <div className="form-group">
                          <label for="">Message </label
                          ><span style={{color: 'red'}} className="required">*</span>
                          <textarea
                            name="description"
                            className="form-control"
                            placeholder="Enter Message Here"
                            required
                          ></textarea>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="contCard">
                        <div className="form-group captcha">
                          <p id="captImg">
                            <img
                              src="https://www.netspacesoftware.in/uploaded/captcha_images/1686643269.6185.jpg"
                              style={{width: '200px' ,  height: '50px' , border: '0'}}
                              alt=" "
                            />
                          </p>
                          <input
                            placeholder="Enter Text In Image"
                            className="form-control"
                            type="text"
                            name="captcha"
                            required
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="submitBtn">
                        <button type="submit">Submit</button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="myMap">
        <div className="myCard">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3667.725548146011!2d75.7926432!3d23.1802149!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3963746dadcb66c5%3A0x96928d86ace22094!2sNetspace%20Software%20Solutions%20Pvt.%20Ltd.!5e0!3m2!1sen!2sin!4v1655366406015!5m2!1sen!2sin"
            width="600"
            height="450"
            style={{border: 0}}
            allowfullscreen=""
            loading="lazy"
            referrerpolicy="no-referrer-when-downgrade"
          ></iframe>
        </div>
      </section>
      
      <section className="ourClient">
        <div className="container">
          <div className="mainHeading text-center">
            <h4>Our Clients</h4>
            <p>
              Excellent customer service is the number one job in any company!
              It is the personality of the company and the reason clients come
              back.
            </p>
          </div>
          <div className="row">
            <div className="clientImgSlide  owl-carousel ">
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/agraseva.png"
                    alt="agraseva netspace"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/bharat.png"
                    alt="bharat bio netspace"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/agrawal.png"
                    alt="agrawal netspace"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/india.png"
                    alt="indiaexpress"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/fino.png"
                    alt="fino bank "
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/boson.png"
                    alt="boson school"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/jcm.png"
                    alt="jcm bazar"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/malva.jpg"
                    alt="malav sandesh"
                  />
                </figure>
              </div>
              <div className="imgCard">
                <figure>
                  <img
                    src="https://www.netspacesoftware.in/assets/images/maharaja.jpg"
                    alt="maharaja international school ujjain"
                  />
                </figure>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
    </>
  )
}

export default Contact