import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { productList } from "../Redux/Slices/Product";
import { serviceList } from "../Redux/Slices/Service";
import { Link } from "react-router-dom";
import { serviceDetails } from "../Redux/Slices/ServiceDetails"
import { productDetails } from "../Redux/Slices/ProductDetails";

const Navbar = () => {
  const dispatch = useDispatch();

  const data = useSelector((state) => {
    return state.servicedata.data.result;
  });
  
  const data1 = useSelector((state) => {
    return state.productdata.data.result;
  });

 
  useEffect(() => {
    dispatch(serviceList());
    dispatch(productList());
  }, []);

  return (
    <>
      <nav className="">
        <div className="topBar">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div className="topBarContent ">
                  <ul className="topBarUl">
                    <li>
                      <figure>
                        <img
                          src="https://www.netspacesoftware.in/assets/images/icons/email.svg"
                          alt="netspace email"
                        />
                      </figure>
                      <a
                        className="hide-xs"
                        href="mailto:info@netspacesoftware.in"
                      >
                        info@netspacesoftware.in
                      </a>
                      <a
                        className="hide-sm"
                        href="mailto:info@netspacesoftware.in"
                      >
                        Mail Us
                      </a>
                    </li>
                    <li>
                      <figure>
                        <img
                          src="https://www.netspacesoftware.in/assets/images/icons/call.svg"
                          alt="netspace call"
                        />
                      </figure>
                      <a className="hide-xs" href="tel:6262558866">
                        +91 6262558866
                      </a>
                      ,&nbsp;
                      <a className="hide-xs" href="tel:0734-4060860">
                        0734-4060860
                      </a>
                      <a className="hide-sm" href="tel:6262558866">
                        Call
                      </a>
                    </li>
                    <li>
                      <figure>
                        <img
                          src="https://www.netspacesoftware.in/assets/images/icons/user.svg"
                          alt="netspace career"
                        />
                      </figure>
                      <a
                        className="hide-xs"
                        href="/netspace-software-vacancy"
                      >
                        CAREER
                      </a>
                    </li>
                  </ul>
                  <ul className="social">
                    <li>Follow Us:</li>
                    <li>
                      <figure>
                        <a
                          href="https://www.facebook.com/netspacesoftwaresolutionspvtltd"
                          target="_blank"
                        >
                          <img
                            src="https://www.netspacesoftware.in/assets/images/icons/facebook.svg"
                            alt="netspace facebook"
                          />
                        </a>
                      </figure>
                    </li>
                    <li>
                      <figure>
                        <a href="skype:live:info_918103" target="_blank">
                          <img
                            src="https://www.netspacesoftware.in/assets/images/icons/skype.svg"
                            alt="netspace skype"
                          />
                        </a>
                      </figure>
                    </li>
                    <li>
                      <figure>
                        <a
                          href="https://www.instagram.com/netspacesoftwaresolutions/"
                          target="_blank"
                        >
                          <img
                            src="https://www.netspacesoftware.in/assets/images/icons/insta.svg"
                            alt="netspace instagram"
                          />
                        </a>
                      </figure>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="container">
          <div className="myHeader">
            <div className="myLogo" href="#">
              <a href="/">
                <img
                  src="https://www.netspacesoftware.in/assets/images/netspace_logo.svg"
                  alt="netspace solftware solutions logo"
                />
              </a>
            </div>
            <div className="myNav">
              <ul className="">
                <li>
                  <a href="/">Home</a>
                </li>
                <li className="SerMenu">
                  <a href="#">Service</a>
                  <ul className="megaMen">
                    {data &&  data.map((val, index) => {
                        return (
                            
                          <li key={index}>
                            <Link to={`/services/${val.Alias}`} onClick={() =>dispatch(serviceDetails(val.Alias))}>{val.Name}</Link>
                          </li>
                        );
                      })}
                  </ul>
                </li>
                <li className="subMenu">
                  <a href="https://www.netspacesoftware.in/netspace-software-product">
                    Products
                  </a>
                  <ul className="megaMenu">
                    <div className="industryWe megaMenuBox">
                      {data1 &&data1.map((val, index) => {
                          return (
                            <div className="indCard" key={index}>
                             
                                <Link to={`/product/${val.Alias}`} onClick={()=>dispatch(productDetails(val.Alias))}>
                                  <div className="indItem">
                                    <figure>
                                      <img
                                        src={`https://www.netspacesoftware.in/uploaded/product/${val.Image}`}
                                        alt={val.Name}
                                      />
                                    </figure>
                                    <p>{val.Name}</p>
                                  </div>
                                </Link>
                             
                            </div>
                          );
                        })}

                      <div className="indCard">
                        <a href="/netspace-software-product">
                          <div className="indItem">view all</div>
                        </a>
                      </div>
                    </div>
                  </ul>
                </li>
                <li>
                  <a href="/hiring">Hire</a>
                </li>
                <li>
                  <a href="/UIUX">
                    UI/UX
                  </a>
                </li>
                <li>
                  <a href="bulksms-services">Bulk SMS</a>
                </li>
                <li>
                  <a href="/netspace-software-portfolio">
                    Portfolio
                  </a>
                </li>
                <li>
                  <a
                    className="myBtn"
                    href="#"
                    data-toggle="modal"
                    data-target="#plan-detail"
                  >
                    Get In Touch
                  </a>
                </li>
              </ul>
              <div className="closeMe">
                <button className="closeMenu">
                  <img
                    src="https://www.netspacesoftware.in/assets/images/icons/Fbt.svg"
                    alt="netspace close menu"
                  />
                </button>
              </div>
            </div>
            <div className="openMenu">
              <img
                src="https://www.netspacesoftware.in/assets/images/icons/menu.svg"
                alt="open menu"
              />
            </div>
          </div>
        </div>
      </nav>

      <div
        id="plan-detail"
        className="modal fade"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="my-modal-title"
        aria-hidden="true"
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="my-modal-title">
                Get In Touch
              </h5>
              <button className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">X</span>
              </button>
            </div>
            <script type="text/javascript"></script>
            <div
              className="alert alert-danger print-error-msg"
              style={{ display: "none" }}
            ></div>
            <form
              action=""
              method="POST"
              id="enquiry"
              encType="multipart/form-data"
            >
              <div className="inline-fields">
                <div className="form-group">
                  <input
                    placeholder="Full Name"
                    className="form-control"
                    type="text"
                    name="name"
                    id="name"
                    required
                  />
                  <span id="name_error" className="text-danger"></span>
                </div>
                <div className="form-group">
                  <input
                    placeholder="Email"
                    className="form-control"
                    type="email"
                    id="email"
                    name="email"
                  />
                  <span id="email_error" className="text-danger"></span>
                </div>
              </div>
              <div className="form-group">
                <input
                  placeholder="Enter Mobile Number *"
                  className="form-control"
                  type="number"
                  name="mobile_no"
                  id="mobile_no"
                  required
                />
                <span id="mobile_error" className="text-danger"></span>
              </div>
              <div className="form-group">
                <textarea
                  name="description"
                  className="form-control"
                  placeholder="Enter Message Here "
                  id="description"
                  required
                ></textarea>
                <span id="description_error" className="text-danger"></span>
              </div>
              {/*<div className="inline-fields">
            <div className="form-group">
              <p>
                <img  src="https://www.netspacesoftware.in/uploaded/captcha_images/1686637524.241.jpg" style="width: 200px; height: 50px; border: 0;" alt=" " /></p>
            </div>
            <div className="form-group">
              <input placeholder="Enter Text In Image *" className="form-control" type="text" name="captcha" id="captcha" required/>
            </div>
    </div> */}
              <div className="form-group">
                <button type="submit" className="btn-started" id="submit">
                  Submit
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Navbar;
