import { configureStore } from '@reduxjs/toolkit';
import productReducer from '../Redux/Slices/Product'
import serviceReducer from "./Slices/Service"
import productDetailsReducer from './Slices/ProductDetails';
import serviceDetailsReducer from './Slices/ServiceDetails';
import  portfolioListReducer from './Slices/PortfolioList';
import portfolioDetailsReducer from './Slices/PortfolioDetails';
    const Store = configureStore({
        reducer: {
            servicedata: serviceReducer,
            productdata: productReducer,
            productDetailsdata: productDetailsReducer,
            serviceDetailsdata:serviceDetailsReducer,
            portfolioListdata:portfolioListReducer,
            portfolioDetailsdata:portfolioDetailsReducer,
        }

});


export default Store