import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const serviceList = createAsyncThunk("serviceList", async () =>{
    const response = await fetch("https://www.netspacesoftware.in/Netspaceapi/GetServiceList",{
        method: "POST"
    })
    return response.json()
})


export const serviceslice = createSlice({
    name: "service",
    initialState: {
        isLoading: false,
        data: [],
        isError: false
    },

    extraReducers: (builder) =>{
        builder.addCase(serviceList.pending, (state, action) =>{
            state.isLoading = true
        });

        builder.addCase(serviceList.fulfilled, (state, action) =>{
            state.isLoading = false;
            state.data = action.payload
        })

        builder.addCase(serviceList.rejected, (state, action) =>{
            state.isError = true
        })

    }


})

export default serviceslice.reducer