import { React, useEffect } from "react";
import { productDetails } from "../Redux/Slices/ProductDetails";
import { useDispatch, useSelector } from "react-redux";
import OurServices from "../components/OurServices";
import OurProducts from "../components/OurProducts";

function Advocate() {

  const dispatch = useDispatch();

  const data = useSelector((state) => {
    return state.productDetailsdata.data.result;
  });
  console.log(data);
  useEffect(() => {
    dispatch(productDetails());
  }, []);

  return (
    <div className="wrapper">
      <section className="videoStatus webDevelop">
        <div className="container">
          <div className="row">
            <div className="col-md-9 col-sm-9">
            {data &&  data.map((val, index) => {
                        return (
                            <div key={index}>
              <div className="videoText">
              
                <h1>{val.Name}</h1>
              </div>
              <div className="portImg">
                <figure>
                  <img
                    src={`https://www.netspacesoftware.in/uploaded/product/${val.Image}`}
                    alt={val.Name}
                  />
                </figure>

                <p dangerouslySetInnerHTML={{ __html: val.ProductDetails }} />
               
              </div>
              </div>
               );
            })}
            </div>

            <div className="col-md-3 col-sm-3">
              <OurServices />

              <OurProducts  />
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Advocate;
