import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const serviceDetails = createAsyncThunk("serviceDetails", async (alias) =>{
    const response = await fetch("https://www.netspacesoftware.in/Netspaceapi/GetServiceDetails",{
        method: "POST",
        headers: new Headers({
            'Content-Type': 'application/x-www-form-urlencoded',
          }),
        body: `alias=${alias}`
    })

    return response.json()
})


export const serviceDetailsslice = createSlice({
    name: "serviceDetails",
    initialState: {
        isLoading: false,
        data: [],
        isError: false
    },

    extraReducers: (builder) =>{
        builder.addCase(serviceDetails.pending, (state, action) =>{
            state.isLoading = true
        });

        builder.addCase(serviceDetails.fulfilled, (state, action) =>{
            state.isLoading = false;
            state.data = action.payload
        })

        builder.addCase(serviceDetails.rejected, (state, action) =>{
            state.isError = true
        })

    }


})

export default serviceDetailsslice.reducer