import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const productDetails = createAsyncThunk("productDetails", async (alias) =>{
    const response = await fetch("https://www.netspacesoftware.in/Netspaceapi/GetProductDetails",{
        method: "POST",
        headers: new Headers({
            'Content-Type': 'application/x-www-form-urlencoded',
          }),
        body: `alias=${alias}`
    })

    return response.json()
})


export const productDetailsslice = createSlice({
    name: "productDetails",
    initialState: {
        isLoading: false,
        data: [],
        isError: false
    },

    extraReducers: (builder) =>{
        builder.addCase(productDetails.pending, (state, action) =>{
            state.isLoading = true
        });

        builder.addCase(productDetails.fulfilled, (state, action) =>{
            state.isLoading = false;
            state.data = action.payload
        })

        builder.addCase(productDetails.rejected, (state, action) =>{
            state.isError = true
        })

    }


})

export default productDetailsslice.reducer