import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const productList = createAsyncThunk("productList", async () =>{
    const response = await fetch("https://www.netspacesoftware.in/Netspaceapi/GetProductList",{
        method: "POST"
    })
    return response.json()
})


export const productslice = createSlice({
    name: "product",
    initialState: {
        isLoading: false,
        data: [],
        isError: false
    },

    extraReducers: (builder) =>{
        builder.addCase(productList.pending, (state, action) =>{
            state.isLoading = true
        });

        builder.addCase(productList.fulfilled, (state, action) =>{
            state.isLoading = false;
            state.data = action.payload
        })

        builder.addCase(productList.rejected, (state, action) =>{
            state.isError = true
        })

    }


})

export default productslice.reducer