import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const portfolioList = createAsyncThunk("portfolioList", async () =>{
    const response = await fetch("https://www.netspacesoftware.in/Netspaceapi/GetPortfolioList",{
        method: "POST"
    })
    return response.json()
})


export const portfolioslice = createSlice({
    name: "portfolioList",
    initialState: {
        isLoading: false,
        data: [],
        isError: false
    },

    extraReducers: (builder) =>{
        builder.addCase(portfolioList.pending, (state, action) =>{
            state.isLoading = true
        });

        builder.addCase(portfolioList.fulfilled, (state, action) =>{
            state.isLoading = false;
            state.data = action.payload
        })

        builder.addCase(portfolioList.rejected, (state, action) =>{
            state.isError = true
        })

    }


})

export default portfolioslice.reducer