import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const portfolioDetails = createAsyncThunk("portfolioDetails", async (Alias) =>{
    const response = await fetch("https://www.netspacesoftware.in/Netspaceapi/GetPortfolioDetails",{
        method: "POST",
        headers: new Headers({
            'Content-Type': 'application/x-www-form-urlencoded',
          }),
        body: `alias=${Alias}`
    })
   
    return response.json()

})



export const portfolioDetailsslice = createSlice({
    name: "portfolioDetails",
    initialState: {
        isLoading: false,
        data: [],
        isError: false
    },

    extraReducers: (builder) =>{
        builder.addCase(portfolioDetails.pending, (state, action) =>{
            state.isLoading = true
        });

        builder.addCase(portfolioDetails.fulfilled, (state, action) =>{
            state.isLoading = false;
            state.data = action.payload
        })

        builder.addCase(portfolioDetails.rejected, (state, action) =>{
            state.isError = true
        })

    }


})

export default portfolioDetailsslice.reducer