import {React ,useEffect} from "react";
import { useDispatch, useSelector } from 'react-redux'
import { productList } from "../Redux/Slices/Product";

const OurProducts = () => {
    const dispatch = useDispatch()

  const data1 = useSelector((state) =>{
    return state.productdata.data.result
})

  useEffect(() =>{
     dispatch(productList())
  }, [])

  return (
    <>
              
                        <div className="DevelopMent proDuct">
                            <h3>Our Products</h3>
                            <ul>
                            {data1 &&  data1.map((val,index) => {
                            return (
                                <li key={index}>  <a  href={val.Alias}>  {val.Name}</a>
                                </li>
                                   );
                                  })}
                                
                            </ul>
                        </div>
                   
    </>
  )
}

export default OurProducts