import React, { useEffect } from 'react'
import Navbar from './components/Navbar'
import Footer from './components/Footer'
import { Route, Routes } from 'react-router-dom'
import Contact from './components/Contact'
import Home from './components/Home'
import Web from './components/Web'
import Advocate from './components/Advocate'
import Hire from './components/Hire'
import Bulk from './components/Bulk'
import { useSelector } from 'react-redux'
import Aos from 'aos';
import 'aos/dist/aos.css'
import UIUX from './components/UIUX'
import Portfoliopage from '../src/components/Portfoliopage'
import Career from './components/Career'
import Portweb from './components/Portweb'
import Portapp from './components/Portapp'
import OurProductlist from './components/OurProductlist'
import Readmore from './components/Readmore'
const App = () => {

  const data = useSelector((state) => {
    return state.servicedata.data.result;
  });
  const data1 = useSelector((state) => {
    return state.productdata.data.result;
  });
 const data2 =useSelector((state)=>{
  return state.portfolioListdata.data.result;
 });

 useEffect(()=>{
             
              Aos.init({
                duration: 1200,
            })

 },[])
  return (
    <>
    <Navbar/>
    <Routes>
      <Route path='/' element={<Home/>}></Route>
      <Route path='/hiring' element={<Hire/>}></Route>
      <Route path='/netspace-software-contact-us' element={<Contact/>}></Route>

      {data &&  data.map((val, index) => {
                        return (
                          <Route key={index} path={`/services/${val.Alias}`} element={<Web/>}></Route>
                           
                          
                        );
                      })}

      <Route path='/Services/Webdesign' element={<Web/>}></Route>

      {data1 &&  data1.map((val, index) => {
                        return (
                          <Route key={index} path={`/product/${val.Alias}`} element={<Advocate/>}></Route>
                        );
                      })}



<Route path='/netspace-software-vacancy' element={<Career/>}></Route>
      <Route path='/bulksms-services' element={<Bulk/>}></Route>
      <Route path='/uiux' element={<UIUX/>}></Route>
      <Route path='/netspace-software-portfolio' element={<Portfoliopage/>}></Route>
      {data2 &&  data2.map((val, index) => {
                        return (
                          <Route key={index} path={`/portfolio-detail/${val.Alias}`} element={<Portweb/>}></Route>                    
                        );
                      })}

{data2 &&  data2.map((val, index) => {
                        return (
                          <Route key={index} path={`/portfolio-detail/${val.Alias}`} element={<Portapp/>}></Route>                    
                        );
                      })}

     
      <Route path='/netspace-software-product' element={<OurProductlist/>}></Route>
      <Route path='/netspace-software-about-us' element={<Readmore/>}></Route>
    </Routes>
    <Footer/>
    
    </>
  )
}

export default App